.wrapper {
    height: 100vh;
    width: 100wh;
    overflow-y: auto;
    overflow-x: hidden;
    perspective: 10px;
  }

header {
    position: relative;
    display: flex;
    height: 0%;
    transform-style: preserve-3d;
    z-index: -1;
}

.layer-10,
.layer-9,
.layer-8,
.background,
.foreground {
    position: absolute;
    height: 100vh;
    object-fit: cover;
    z-index: -1;
}

.background {
    transform: translateZ(-10px) scale(2.15);
  }

.foreground {
    transform: translateZ(-5px) scale(1.5);
}

.layer-10 {
    transform: translateZ(-10px) scale(2.15);
}

.layer-9 {
    transform: translateZ(-9px) scale(2.05);
}

.layer-8 {
    transform: translateZ(-6px) scale(1.73)
}

.text-adjust {
    margin-top: 35vh;
}

.gradient-back  {
    background: #DAE2F8;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to top, #D6A4A4, #DAE2F8);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to top, #D6A4A4, #DAE2F8); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    opacity: 95%;
}


.loader {
    width: 60%;
    height: 20px;
    background: 
        linear-gradient(90deg,#0001 33%,#0005 50%,#0001 66%)
        #f2f2f2;
    background-size:300% 100%;
    animation: l1 1s infinite linear;
    opacity: 60%;
    border-radius:  0.75rem;
}

@keyframes l1 {
    0% {background-position: right}
}